






















































import { Component, Vue } from 'vue-property-decorator'
import TableList from '@/components/TableList/index.vue'
import Pagination from '@/components/Pagination/index.vue'
import { fetchEditInfo } from '@/api/sysManage/userList'
import Edit from './bindEdit.vue'
import { ListQuery } from '@/domains/listQuery'
import { Column } from '@/domains/column'
import { Sort } from '@/domains/sort'
import { QqUser } from '@/domains/user'
import { Pages } from '@/domains/pages'
import sortable from '@/utils/sortable'
import { checkSelection } from '@/utils/checkSelection'

class Query extends ListQuery {
  public nickname: string = ''
}

class Page extends Pages {
  public list: QqUser[] = []
}

@Component({
  name: 'BindUserList',
  components: {
    TableList,
    Pagination,
    Edit
  },
  filters: {
    statusFilter(status: number) {
      const statusMap = ['danger', 'success']
      return statusMap[status]
    },
    statusTextFilter(status: number) {
      const statusMap = ['禁用', '正常']
      return statusMap[status]
    }
  }
})
export default class BindUserList extends Vue {
  /** 是否显示table列表 */
  private isShowList: boolean = true
  /** 查询条件 */
  private listQuery: Query = new Query()
  /** 数据列表 */
  private list: QqUser[] = []
  /** 数据列表的id集合，用于拖拽列表 */
  private newList: string[] = []
  /** 数据模型 */
  private columns: Column[] = [
    {
      label: '用户ID',
      key: 'userId'
    },
    {
      label: '用户名',
      key: 'username'
    },
    {
      label: '邮箱',
      key: 'email'
    },
    {
      label: '手机号',
      key: 'mobile'
    },
    {
      label: '状态',
      key: 'status',
      slot: 'status'
    },
    {
      label: '创建时间',
      key: 'createTime',
      width: 150
    }
  ]
  /** 数据总数 */
  private total: number = 0
  /** 新增还是编辑 */
  private type: string = ''
  /** 当前编辑数据的ID */
  private id: string = '0'
  /** 当前选中的数据 */
  private multipleSelection: QqUser[] = []
  /** 唯一标识id的key值 */
  private idKey: string = 'userId'
  created(): void {
    this.getList()
  }
  /** 获取数据列表 */
  private getList(): void {
    fetchEditInfo(this.$route.query.userId as string).then((response: any) => {
      this.list = [response.user]
    })
  }
  // 修改
  handleEdit() {
    checkSelection(this.multipleSelection, 'one').then(() => {
      this.id = this.multipleSelection[0].userId
      this.type = 'edit'
      this.isShowList = false
    })
  }
  // 选择行
  handleSelectionChange(val: QqUser[]) {
    this.multipleSelection = val
  }
  // 监听子组件，执行方法
  getMsgFromChild(v: string): void {
    if (v !== 'cancel') {
      this.getList()
    }
    this.isShowList = true
  }
}
