/** 查询条件 */
export default class ListQuery {
  public static build(data: ListQuery) {
    const listQuery: ListQuery = new ListQuery()
    listQuery.page = data.page
    listQuery.limit = data.limit
    listQuery._search = data._search
    listQuery.sidx = data.sidx
    listQuery.order = data.order
    return listQuery
  }

  public static builds(datas: ListQuery[]) {
    const listQuerys: ListQuery[] = []
    datas.forEach((data: ListQuery) => {
      const listQuery: ListQuery = this.build(data)
      listQuerys.push(listQuery)
    })
    return listQuerys
  }

  /** 当前页数 */
  public page: number = 1
  /** 查询条数 */
  public limit: number = 10
  /** 是否启用搜索 */
  public _search: boolean = true
  /** 排序字段 */
  public sidx: string = ''
  /** 排序方式 desc降序, asc升序 */
  public order: string = 'desc'
}
