/** 排序条件 */
export default class User {
  public static build(data: User) {
    const user: User = new User()
    user.createTime = data.createTime
    // user.createUserId = data.createUserId
    // user.credentialsSalt = data.credentialsSalt
    user.email = data.email
    user.mobile = data.mobile
    user.password = data.password
    user.roleIdList = data.roleIdList
    // user.salt = data.salt
    user.status = data.status
    user.userAvatar = data.userAvatar
    user.userId = data.userId
    user.userType = data.userType
    user.username = data.username
    return user
  }

  public createTime: string = ''
  public createUserId: number = 0
  // public credentialsSalt: string = ''
  public email: string = ''
  public mobile: string = ''
  public password: string = ''
  public roleIdList: string[] = []
  // public salt: string = ''
  public status: number = 0
  public userAvatar: string = ''
  public userId: string = '0'
  public userType: number = 0
  public username: string = ''
  /** 索引 */
  readonly [roleId: string]: string | number | string[]
}
