import { MessageBox } from 'element-ui'

export function checkSelection(arr: any[], type?: string): Promise<void> {
  /**
   * @param { arr } arr multipleSelection当前选中数据
   * @param { type } string 检测类型，one：只能选择一条，没有参数可以选择多条
  */
  return new Promise((resolve, reject) => {
    if (arr.length === 0) {
      MessageBox.alert('请选择一条记录')
    } else {
      if (type === 'one') {
        if (arr.length > 1) {
          MessageBox.alert('只能选择一条记录')
        } else {
          resolve()
        }
      } else {
        resolve()
      }
    }
  })
}
