import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'
import { User } from '@/domains/user'

export function fetchList(query: ListQuery) {
  return request({
    url: '/sys/user/list',
    method: 'get',
    params: query
  })
}

export function fetchAdminList() {
  return request({
    url: '/sys/role/select',
    method: 'get'
  })
}

export function saveData(data: User) {
  return request({
    url: '/sys/user/save',
    method: 'post',
    data
  })
}

export function deleteData(data: string[]) {
  return request({
    url: '/sys/user/delete',
    method: 'delete',
    data
  })
}

export function updateData(data: User) {
  return request({
    url: '/sys/user/update',
    method: 'put',
    data
  })
}

export function fetchEditInfo(query: string) {
  const url = '/sys/user/getInfo/' + query
  return request({
    url,
    method: 'get'
  })
}

export function fetchUserType() {
  return request({
    url: '/sys/user/usertype',
    method: 'get'
  })
}
