import { ElTableColumn } from 'element-ui/types/table-column'

interface Sort {
  column: ElTableColumn,
  order: 'ascending' | 'descending',
  prop: string
}

export const SortChange = function(sort: Sort) {
  if (sort.column === null) {
    return {
      order: ''
    }
  } else {
    if (sort.order === 'descending') {
      return {
        order: 'desc',
        sidx: sort.prop
      }
    } else {
      return {
        order: 'asc',
        sidx: sort.prop
      }
    }
  }
}
