




















































/**
 * 作用: 数据表格显示列表
 * Auth: Ke.Chunpeng
 * Created: 2019/4/19-13:59
 * props
 *   data: 需要显示的数据
 *   columns: 数据模型，label为标题名称，key为数据key值，width为宽度，slot为当数据需要特殊处理时可用
 *   id: 排序关键值，一般为单条记录的id唯一标识符
*/

import { Component, Vue, Prop } from 'vue-property-decorator'
import { ElTableColumn } from 'element-ui/types/table-column'
import { SortChange } from '@/utils/sortChange'
import { Column } from '@/domains/column'

interface Sort {
  column: ElTableColumn,
  order: 'ascending' | 'descending',
  prop: string
}

@Component({
  name: 'TableList'
})
export default class TableList extends Vue {
  /** 需要显示的数据 */
  @Prop({ default: [], required: true })
  private data!: any[]
  /** 数据模型，label为标题名称，key为数据key值，width为宽度，slot为当数据需要特殊处理时可用 */
  @Prop({ default: [], required: true })
  private columns!: Column[]
  /** 排序关键值，一般为单条记录的id唯一标识符 */
  @Prop({ default: 'id', required: true })
  private rowKey!: string

  // 图标显示
  private iconShow(index: number, record: any) {
    return (index === 0 && record.children && record.children.length > 0)
  }
  private handleSelectionChange(val: any) {
    this.$emit('handleSelectionChange', val)
  }
  // 排序
  private sortChange(sort: Sort) {
    this.$emit('handleSortChange', SortChange(sort))
  }
  // 大写字母转成_小写
  private toLowerCase(val: string) {
    return val.replace(/([A-Z])/g, '_$1').toLowerCase()
  }
}
