







































































































import { fetchAdminList, saveData, updateData, fetchEditInfo, fetchUserType } from '@/api/sysManage/userList'
import { Component, Vue, Prop } from 'vue-property-decorator'
import { User } from '@/domains/user'

@Component({
  name: 'BindEdit',
  components: {
  }
})
export default class Edit extends Vue {
  @Prop()
  private id!: string
  @Prop({ default: 'save' })
  private type!: string
  /** form数据 */
  private ruleForm: User = new User()
  /** form校验 */
  private rules: any = {
    username: [
      { required: true, message: '请输入用户名', trigger: 'blur' },
      { min: 3, max: 16, message: '长度在 3 到 16 个字符', trigger: 'blur' }
    ],
    password: [
      { required: true, message: '请输入密码', trigger: 'blur' },
      { min: 3, max: 32, message: '长度在 3 到 32 个字符', trigger: 'blur' }
    ],
    email: [
      { required: true, message: '请输入邮箱', trigger: 'blur' },
      { min: 3, max: 32, message: '长度在 3 到 32 个字符', trigger: 'blur' },
      { pattern: /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/, message: '请输入正确邮箱' }
    ],
    mobile: [
      { pattern: /^1[34578]\d{9}$/, message: '目前只支持中国大陆的手机号码' }
    ],
    roleIdList: [
      { type: 'array', required: true, message: '请至少选择一个角色', trigger: 'change' }
    ]
  }
  /** 角色数据列表，提供选择 */
  private adminList: any[] = []
  /** 用户类型列表，提供选择 */
  private userTypeList: any[] = []
  private created() {
    if (this.type === 'edit') {
      this.getEditInfo()
    }
    this.getAdminList()
    this.getUserTypeList()
  }
  // 校验提交
  private submitForm(formName: string) {
    let refs: any = this.$refs[formName]
    refs.validate((valid: boolean) => {
      if (valid) {
        if (this.type === 'edit') {
          this.handleEdit()
        } else {
          this.handleSubmit()
        }
      } else {
        console.log('error submit!!')
        return false
      }
    })
  }
  // 保存
  private handleSubmit() {
    saveData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 修改
  private handleEdit() {
    updateData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 取消
  private cancel() {
    this.$emit('msg-from-child', 'cancel')
  }
  // 获取adminlist列表
  private getAdminList() {
    fetchAdminList().then((response: any) => {
      this.adminList = response.list
    })
  }
  // 获取编辑信息
  private getEditInfo(): void {
    fetchEditInfo(this.id).then((response: any) => {
      const data: User = response.user
      this.ruleForm = User.build(data)
    }).catch(err => {
      console.log(err)
    })
  }
  private getUserTypeList(): void {
    this.userTypeList = ['后台用户', '前端用户', '微信用户', 'QQ用户', '微博用户', '超级用户']
    // fetchUserType().then(response => {
    //   const arr = []
    //   console.log(response)
    //   for (const key in response.data.userTypes) {
    //     arr.push(response.data.userTypes[key])
    //   }
    //   this.userTypeList = arr
    //   console.log(this.userTypeList)
    // }).catch((error) => {
    //   console.log(error)
    // })
  }
}
